* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.menu-list {
  flex-direction: column;
  gap: 6px;
  display: flex;
}

li {
  list-style: none;
}

.button {
  min-width: 150px;
  text-align: center;
  cursor: pointer;
  color: #000;
  background-color: rgba(5, 125, 15, .6);
  border: none;
  border-radius: 10px;
  padding: 8px;
  font-size: 16px;
  line-height: 1;
  text-decoration: none;
  display: inline-block;
}

.disabled {
  color: rgba(0, 0, 0, .4);
  background-color: rgba(5, 125, 15, .4);
}

.container {
  margin-top: 30px;
  margin-left: 30px;
}

.input-container {
  margin-bottom: 20px;
}

#datetime-picker {
  height: 32px;
  width: 255px;
  margin-right: 10px;
}

.timer {
  text-align: center;
  gap: 10px;
  display: flex;
}

.field {
  flex-direction: column;
  display: flex;
}

.value {
  font-size: 36px;
}

.label {
  text-transform: uppercase;
  font-size: 14px;
}

